<template>
    <div style="top:100px;width:300px">
        <el-form :model="form" label-width="120px" :rules="rules">
            <el-form-item label="" required>
                <el-input v-model="form.userName" auto-complete="off" class="el-col-width" required></el-input>
            </el-form-item>
            <el-form-item label="" required>
                <el-input type="password" v-model="form.passWord" auto-complete="off" class="el-col-width" required></el-input>
            </el-form-item>
            <el-form-item>
              <el-checkbox v-model="form.rememberMe"></el-checkbox>
            </el-form-item>
            <el-form-item label="" required>
                <el-input v-model="form.imageCode" auto-complete="off" class="el-col-width"></el-input>
            </el-form-item>
            <el-form-item required>
                <img id="img" alt="" onclick="this.src='/createImageCode?d='+new Date()*1" src="/createImageCode" />
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="handleSubmit"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
import HTTP from '@/api/user'
export default {
  data() {
    return {
      form: {
        userName: '',
        passWord: '',
        rememberMe: false,
      },
      rules: {
        userName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        passWord: [
          { required: true, message: '', trigger: 'blur' }
        ],
        imageCode: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },

  methods: {
    handleSubmit() {
      var params = {}
      params.userName = this.form.userName
      params.passWord = this.form.passWord
      params.rememberMe = this.form.rememberMe
      params.imageCode = this.form.imageCode
          HTTP.login( params).then(r => {
            //,
            if (r.data.code === 200) {
              this.$router.push({ name: 'home', params: { user: r.data } })
              this.$notify({
                title: 'success',
                message: '',
                type: 'success',
                position: 'bottom-right'
              })
            } else {
                document.getElementById("img").src="/createImageCode?d='+new Date()*1"; //
              this.$notify.error({
                title: 'error',
                message: ':' + r.data.msg,
                type: 'error',
                position: 'bottom-right'
              })
            }
          })

  },
   handleLogout() {
      var params = {}
      HTTP.logout(params).then(r => {
        // 
          this.$router.push('login')
      })
    }
}
}
</script>



